import { useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { fetchProfile } from "../../../../redux/slices/profileSlice";
import { createOnlinePayment } from "../../../../api/login/Login";
import { toast } from "react-toastify";
import Select from "react-select";

function OnlinePaymentRequest() {
  const [loader, setLoader] = useState(false);
  const [emailError, setEmailError] = useState("");

  const dispatch = useDispatch();
  const profileData = useSelector((state) => state.profile?.data);

  const [onlineData, setOnlineData] = useState({
    payment_from: "",
    amount: "",
    method: "",
    lat: "",
    long: "",
    mobile: "",
    email: "",
    username: "",
    area_pincode: "",
    address: "",
  });

  useEffect(() => {
    dispatch(fetchProfile());
  }, [dispatch]);

  useEffect(() => {
    if (onlineData.payment_from === "customer") {
      setOnlineData((prev) => ({
        ...prev,
        username: "",
        email: "",
        mobile: "",
        area_pincode: "",
        address: "",
      }));
    }

    if (onlineData.payment_from === "own" && profileData) {
      setOnlineData((prev) => ({
        ...prev,
        username: profileData?.name || "",
        email: profileData?.email || "",
        mobile: profileData?.mobile || "",
        area_pincode: profileData?.pinCode || "",
        address: profileData?.presentAddr || "",
      }));
      setEmailError("");
    }
  }, [onlineData.payment_from, profileData]);

  const validateEmail = (email) => {
    const regex =
      /^(?!.*\.\.)(?!.*\.$)[a-zA-Z0-9._%+-]+@[a-zA-Z0-9-]+(?:\.[A-Za-z]{2,})+$/;
    return regex.test(email);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;

    setOnlineData((prev) => ({ ...prev, [name]: value }));

    if (name === "email") {
      if (!value) {
        setEmailError("Email is required.");
      } else if (!validateEmail(value)) {
        setEmailError("Please enter a valid email address (e.g., example@gmail.com).");
      } else {
        setEmailError("");
      }
    }
  };

  const handleSelectChange = (name, selected) => {
    setOnlineData({ ...onlineData, [name]: selected?.value || "" });
  };

  const submitOnlinePayment = async () => {
    if (!onlineData.payment_from) return toast.error("Select payment source");
    if (!onlineData.username) return toast.error("Full Name required");
    if (!onlineData.mobile) return toast.error("Mobile required");
    if (!onlineData.email) return toast.error("Email required");
    if (emailError) return toast.error("Invalid Email");
    if (!onlineData.address) return toast.error("Address required");
    if (!onlineData.area_pincode) return toast.error("Pincode required");
    if (!onlineData.method) return toast.error("Payment method required");
    if (!onlineData.amount) return toast.error("Amount required");

    setLoader(true);

    try {
      const res = await createOnlinePayment(onlineData);
      const result = res?.data;

      if (result?.success && result?.data?.url) {
        toast.success("Redirecting...");
        setTimeout(() => window.open(result.data.url, "_blank"), 500);
      } else {
        toast.error(result?.message || "Payment failed");
      }
    } catch {
      toast.error("Network Error");
    } finally {
      setLoader(false);
    }
  };

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(
      (pos) =>
        setOnlineData((prev) => ({
          ...prev,
          lat: pos.coords.latitude,
          long: pos.coords.longitude,
        })),
      () => toast.warning("Location disabled")
    );
  }, []);

  const inputStyle = {
    height: "45px",
    borderRadius: "12px",
    backgroundColor: "var(--bs-primary3)",
    color: "var(--bs-primary2)",
    border: "1px solid var(--bs-primary2)",
    paddingLeft: "10px",
    fontSize: "14px",
    width: "100%",
  };

  const reactSelectStyles = {
    control: (p) => ({
      ...p,
      backgroundColor: "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      borderColor: "var(--bs-primary2)",
      height: "45px",
      borderRadius: "12px",
      fontSize: "14px",
      minHeight: "45px",
      cursor: "pointer",
      boxShadow: "none",
    }),
    singleValue: (p) => ({ ...p, color: "var(--bs-primary2)" }),
    placeholder: (p) => ({ ...p, color: "var(--bs-primary2)" }),
    menu: (p) => ({ ...p, backgroundColor: "var(--bs-primary3)" }),
    option: (p, s) => ({
      ...p,
      backgroundColor: s.isFocused ? "#99999950" : "var(--bs-primary3)",
      color: "var(--bs-primary2)",
      cursor: "pointer",
    }),
  };

  return (
    <div className="PageHeading">
      <h1>Online Payment Request</h1>

      <div className="ContentArea">
        <div
          className="card"
          style={{
            background: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            border: "1.5px solid var(--bs-primary2)",
            borderRadius: "16px",
            padding: "20px",
          }}
        >
          <div className="card-body">
            <form>
              <div
                className="form-row"
                style={{
                  border: "1px solid var(--bs-primary2)",
                  padding: "15px",
                  borderRadius: "16px",
                }}
              >
                <div className="form-group col-md-3">
                  <label style={{ fontWeight: 700 }}>Add Amount From *</label>

                  <Select
                    value={
                      onlineData.payment_from
                        ? {
                            value: onlineData.payment_from,
                            label:
                              onlineData.payment_from === "own"
                                ? "Own Account"
                                : "Customer Account",
                          }
                        : null
                    }
                    onChange={(selected) =>
                      handleSelectChange("payment_from", selected)
                    }
                    options={[
                      { value: "own", label: "Own Account" },
                      { value: "customer", label: "Customer Account" },
                    ]}
                    styles={reactSelectStyles}
                    placeholder="Select Source"
                    isDisabled={onlineData.payment_from !== ""}
                    isSearchable={false}
                  />
                </div>

                {onlineData.payment_from !== "" && (
                  <>
                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Full Name *</label>
                      <input
                        className="form-control"
                        name="username"
                        disabled={onlineData.payment_from === "own"}
                        placeholder="Enter Full Name"
                        value={onlineData.username}
                        onChange={handleChange}
                        style={inputStyle}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Mobile *</label>
                      <input
                        className="form-control"
                        name="mobile"
                        maxLength={10}
                        disabled={onlineData.payment_from === "own"}
                        placeholder="Enter Mobile"
                        value={onlineData.mobile}
                        onChange={handleChange}
                        style={inputStyle}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Email *</label>
                      <input
                        className="form-control"
                        type="email"
                        name="email"
                        disabled={onlineData.payment_from === "own"}
                        placeholder="Enter Email"
                        value={onlineData.email}
                        onChange={handleChange}
                        style={inputStyle}
                      />
                      {emailError && (
                        <p
                          style={{
                            fontSize: "12px",
                            color: "red",
                            fontWeight: 600,
                            marginTop: "4px",
                            marginBottom: 0,
                          }}
                        >
                          {emailError}
                        </p>
                      )}
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Address *</label>
                      <input
                        className="form-control"
                        name="address"
                        disabled={onlineData.payment_from === "own"}
                        placeholder="Enter Address"
                        value={onlineData.address}
                        onChange={handleChange}
                        style={inputStyle}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Pincode *</label>
                      <input
                        className="form-control"
                        name="area_pincode"
                        maxLength={6}
                        disabled={onlineData.payment_from === "own"}
                        placeholder="Enter Pincode"
                        value={onlineData.area_pincode}
                        onChange={handleChange}
                        style={inputStyle}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Payment Method *</label>

                      <Select
                        value={
                          onlineData.method
                            ? { value: onlineData.method, label: onlineData.method.toUpperCase() }
                            : null
                        }
                        onChange={(selected) =>
                          handleSelectChange("method", selected)
                        }
                        options={[
                          { value: "upi", label: "UPI" },
                          { value: "card", label: "Card" },
                          { value: "netbanking", label: "Net Banking" },
                          { value: "wallet", label: "Wallet" },
                        ]}
                        placeholder="Select Method"
                        isSearchable={false}
                        styles={reactSelectStyles}
                      />
                    </div>

                    <div className="form-group col-md-3">
                      <label style={{ fontWeight: 700 }}>Amount *</label>
                      <input
                        className="form-control"
                        type="number"
                        name="amount"
                        placeholder="Enter Amount"
                        value={onlineData.amount}
                        onChange={handleChange}
                        style={inputStyle}
                      />
                    </div>

                    <div className="form-group col-md-12 text-center mt-3">
                      <button
                        type="button"
                        disabled={
                          loader ||
                          emailError ||
                          !onlineData.username ||
                          !onlineData.mobile ||
                          !onlineData.email ||
                          !onlineData.address ||
                          !onlineData.area_pincode ||
                          !onlineData.method ||
                          !onlineData.amount
                        }
                        onClick={submitOnlinePayment}
                        style={{
                          borderRadius: "50px",
                          padding: "12px 45px",
                          backgroundColor:
                            !loader &&
                            !emailError &&
                            onlineData.username &&
                            onlineData.mobile &&
                            onlineData.email &&
                            onlineData.address &&
                            onlineData.area_pincode &&
                            onlineData.method &&
                            onlineData.amount
                              ? "var(--bs-primary3)"
                              : "#777",
                          color: "var(--bs-primary2)",
                          border: "1px solid var(--bs-primary2)",
                          fontWeight: "bold",
                          fontSize: "16px",
                          letterSpacing: "0.5px",
                          cursor:
                            !loader &&
                            !emailError &&
                            onlineData.username &&
                            onlineData.mobile &&
                            onlineData.email &&
                            onlineData.address &&
                            onlineData.area_pincode &&
                            onlineData.method &&
                            onlineData.amount
                              ? "pointer"
                              : "not-allowed",
                        }}
                      >
                        {loader ? "Processing..." : "Submit Request"}
                      </button>
                    </div>
                  </>
                )}
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  );
}

export default OnlinePaymentRequest;
